.App {
  position: absolute;
  top: 45%;
  left: 45%;
  text-align: center;
  
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.label {
  font-weight: bold;
}

.center {
  text-align: center;
}

.checkPass {
  font-size: 12px;
}

.default {
  color: grey;
}

.success {  
  color: green;
}

.error {  
  color: red;
  font-size: 12px;
}

.rules {   
  font-size: 12px;
  list-style: none;  
}
